@import url(https://fonts.googleapis.com/css?family=Marvel);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Marvel', sans-serif;
  background: linear-gradient(to right,#2193b0 , #6dd5ed);
  color: #fff;
  /* min-height: 100vh; */
  margin-top: 2em;
  display: grid;
  align-content: center;
  justify-content: center;
}
h4{
  color: #66c7e0;
}
#container{
  display: grid;
  min-width: 20em;
  height: 100%;
  border: 2px solid #333;
  grid-template-rows: 4fr 6fr;
  box-shadow: 4px 0px 8px rgba(48, 36, 36, 0.7);
  background: linear-gradient(to bottom, #302424 0%,#403534 51%,#655e5a 100%); 
}
.display{
  border-bottom:1px solid #333;
  padding: 0.8em 1em;
  display: flex;
  flex-direction: column;
  text-align: right;
  justify-content: flex-end;
}
#pad{
  display: grid;
  grid-template-columns: repeat(4,80px);
  grid-template-rows: repeat(5,60px);
  grid-template-areas: 'clear clear clear divide'
                       'seven  eight nine multiply'
                       'four  five  six   subtract'
                       'one two     three   add'
                       'zero  zero  decimal equals'
}
#pad > * {

  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2em;
  font-weight: thin;
  cursor: pointer;
}
#pad > *:hover{
  background:#C9F1FA;
}
#zero{
  grid-area: zero;
}
#one{
  grid-area: one;
}
#two{
  grid-area: two;
}
#three{
  grid-area:three;
}
#four{
  grid-area:four;
}
#five{
  grid-area:five;
}
#six{
  grid-area:six;
}
#seven{
  grid-area:seven;
}
#eight{
  grid-area:eight;
}
#nine{
  grid-area:nine;
}
#clear{
  grid-area:clear;
  color: #66c7e0;
}
#add{
  grid-area: add;
}
#subtract{
  grid-area:subtract;
}
#divide{
  grid-area: divide;
}
#multiply{
  grid-area: multiply;
}
#decimal{
  grid-area: decimal;
}
#equals{
  grid-area:equals;
}
#add,#multiply,#equals,#divide,#subtract{
  background:#66c7e0;
}
